<template>
  <div class="page-wrapper buses-page">
    <!-- App Header -->
    <AppHeader layoutType="layout-2"></AppHeader>

    <section class="viewport-h-min-100">
      <!-- Include AppLoader (Show Progress On Page) -->
      <AppLoader v-if="show_Loader" />

      <div class="1">
        <!-- Loader -->
        <div
          class="loader bus-progress-loader flexbox align-items-center justify-center zindex-10"
          v-show="show_Loader"
        >
          <div class="text-primary">
            <i class="fas fa-spinner fa-pulse fa-5x"></i>
          </div>
        </div>
        <!--Loader-->

        <div class="container p-1">
          <div
            class="title-wrapper with-icon pos-relative flexbox justify-center align-items-center p-t-xs p-b-xs mr-t-2 mr-b-2"
          >
            <span class="p-l-2 p-r-2 mobile-p-l-0 mobile-p-r-0 align-items-center zindex-5">
              <img src="@/assets/icons/passanger-icon.svg" alt="Book Bus" />
              <h3 class="title font-h-2 mobile-font-sm">Passanger Details</h3>
            </span>
          </div>

          <div class="form-wrapper p-t-1 p-b-1">
            <form
              class="form-inline passanger-form"
              action="#"
              method="post"
              @submit.prevent="passangerFormSubmittedEvent"
            >
              <div
                class="passanger-row flexbox justify-between p-b-2 mobile-flex-dir-col flex-wrap"
              >
                <div
                  class="single-passanger w-50-gap mobile-w-100 mobile-mr-b-2"
                  v-for="(passanger_detail, index) in passenger_details"
                  :key="index"
                >
                  <h2 class="title font-h-2 mr-0 mr-b-1">Passanger {{ index + 1}}</h2>
                  <div class="form-row">
                    <div class="form-group w-50-gap mobile-w-100">
                      <label>Full Name</label>
                      <input
                        type="text"
                        name="p_f_name[]"
                        placeholder="Full Name"
                        required
                        class="form-control"
                        v-model="passenger_details[index].passenger_name"
                        @blur="validateFullNames(passenger_details[index].passenger_name)"
                        @keyup.enter="validateFullNames(passenger_details[index].passenger_name)"
                      />
                    </div>
                    <div class="form-group w-50-gap mobile-w-100">
                      <label>ID/Passport</label>
                      <input
                        type="tel"
                        name="p_l_name[]"
                        placeholder="ID/Passport Number"
                        required
                        class="form-control"
                        v-model="passenger_details[index].passenger_id_number"
                      />
                    </div>
                  </div>
                  <div class="form-row">
                    <div class="form-group w-50-gap mobile-w-100">
                      <label>Gender</label>
                      <select
                        class="form-control"
                        name="p_gender[]"
                        v-model="passenger_details[index].passenger_gender"
                        required
                      >
                        <option value>Select Gender</option>
                        <option value="Male">Male</option>
                        <option value="Female">Female</option>
                      </select>
                    </div>
                    <div class="form-group w-50-gap mobile-w-100">
                      <label>Nationality</label>
                      <select
                        class="form-control"
                        name="p_nationality[]"
                        v-model.number="passenger_details[index].passenger_nationality_id"
                        required
                      >
                        <option value>Select Nationality</option>
                        <option
                          :value="country.id"
                          v-for="(country, index) in bus_seats.countries"
                          :key="index"
                        >{{ country.country_name }}</option>
                      </select>
                    </div>
                  </div>
                  <div class="form-row">
                    <div class="form-group w-50-gap mobile-w-100">
                      <label>Email</label>
                      <input
                        type="email"
                        name="p_email[]"
                        placeholder="Email"
                        required
                        class="form-control"
                        v-model="passenger_details[index].passenger_email"
                      />
                    </div>
                    <div class="form-group w-50-gap mobile-w-100">
                      <label>Phone Number</label>
                      <div class="input-pair">
                        <select
                          class="form-control country-code selector"
                          v-model="passenger_details[index].passenger_phone_code"
                          required
                        >
                          <option value>Code</option>
                          <option
                            :value="country.country_phonecode"
                            v-for="(country, index) in bus_seats.countries"
                            :key="index"
                          >{{ country.country_name_phonecode }}</option>
                        </select>
                        <input
                          type="tel"
                          name="p_phone[]"
                          placeholder="Phone Number"
                          required
                          class="form-control"
                          v-model="passenger_details[index].passenger_phone_number"
                          @blur="validatePhoneNumber(passenger_details[index].passenger_phone_number)"
                          @keyup.enter="validatePhoneNumber(passenger_details[index].passenger_phone_number)"
                        />
                      </div>
                    </div>
                  </div>
                  <div class="form-row">
                    <div class="form-group w-50-gap mobile-w-100">
                      <label>Book Returning?</label>
                      <select
                        class="form-control"
                        name="p_returning[]"
                        v-model.number="passenger_details[index].book_return"
                        @change="checkReturn($event, index, passenger_details[index].return_seat)"
                        required
                      >
                        <option value>Select</option>
                        <option value="1">Yes</option>
                        <option value="0">No</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>

              <!-- Show form feedback here -->
              <div
                class="form-group form-message text-center p-t-0 hidden"
              >Enter some form feedback here or error message</div>
              <!-- Show form feedback here -->

              <div class="form-group btn-wrapper-inline text-center p-t-sm -p-b-sm">
                <button class="btn btn-inline mr-auto">PROCEED TO REVIEW TRIP</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </section>

    <!-- App Footer -->
    <AppFooter />
  </div>
</template>

<script>
/*Vuex Data & Actions*/
import { mapGetters, mapActions } from "vuex";

//AppHeader component
import AppHeader from "@/components/AppHeader.vue";

//AppTLoader component
import AppLoader from "@/components/AppLoader.vue";

//AppFooter component
import AppFooter from "@/components/AppFooter.vue";

export default {
  name: "AppPassangerForm",
  components: {
    AppHeader,
    AppLoader,
    AppFooter
  },
  computed: mapGetters([
    "trip",
    "passenger_details",
    "bus_seats",
    "show_Loader",
    "trip_info"
  ]),
  mounted() {
    //scroll to top
    window.scrollTo(0, 0);
  },
  methods: {
    ...mapActions([
      "vehicleSelected",
      "getSubRequest",
      "getReturningDetails",
      "getTripInfo",
      "showLoader",
      "hideLoader",
      "getActiveReturningIndex",
      "removeReturningDetails",
      "removeUnSelectedSeats",
      "getTotalAmount",
      "showWarning",
      "showError"
    ]),
    //Check Return
    checkReturn(event, passanger, seat) {
      if (event.target.value == 1) {
        //Set returning index
        this.getActiveReturningIndex(passanger);
        //Set Returning details
        this.getReturningDetails();
        //Ajax call for the return buses
        this.getSubRequest({
          town_from: this.trip.town_from_return,
          town_to: this.trip.town_to_return,
          travelling_on: this.trip.returning_on,
          is_return: true
        });
        //Redirect to Return Bus seats page
        this.$router.push("ReturnBus");
      } else {
        //Set returning index
        this.getActiveReturningIndex("");
        //Remove Return Details
        this.removeReturningDetails(passanger);
        this.removeUnSelectedSeats({ seat: seat, is_return: true });
        this.getTotalAmount({ is_return: true });
      }
    },
    //Submit Details
    passangerFormSubmittedEvent() {
      //Show Loader
      this.showLoader();

      //Trip Details
      this.getTripInfo();

      //Submit data request
      this.$http
        .post("http://193.200.241.91:8000/api/v2/tickets", this.trip_info)
        .then(
          response => {
            console.log(response);

            //Hide Loader
            this.hideLoader();

            //scroll to top
            window.scrollTo(0, 0);

            //show review
            this.$router.push("TripReview");
          },
          error => {
            console.log(error);
            //Hide Loader
            this.hideLoader();

            //Alert
            this.showError("Could not complete request");

          }
        );
    },
    //Validate Full Names
    validateFullNames(full_names) {
      if (full_names.trim().split(" ").length <= 1) {
        //Alert
        this.showWarning("Kindly fill at least 2 names");

      }
    },
    validatePhoneNumber(phone_number) {
      //Regex to validate Phone Number
      let to_match = /^(?:254|\+254|0)?(7(?:(?:[0-9][0-9])|(?:0[0-8])|(4[0-1]))[0-9]{6})$/;
      let check_validity = to_match.test(phone_number);
      if (!check_validity) {

        //Alert
        this.showWarning("Incorrect phone number format");

      }
    }
  }
};
</script>
