import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import Contact from '../views/Contact.vue'
import Buses from '../views/Buses.vue'
import Services from '../views/Services.vue'
import Faqs from '../views/Faqs.vue'
import Portal from '../views/Portal.vue'
//Passanger Form Page
import Passangers from '../components/AppPassangerForm.vue'
//Return Bus Results
import ReturnBus from '../components/AppReturnBusResults.vue'
//Bus seats
import BusSeats from '../components/AppBusSeats.vue'
//Return Bus seats
import ReturnBusSeats from '../components/AppReturnBusSeats.vue'
//Payment
import Payment from '../components/AppPayment.vue'
//Return Bus seats
import TripReview from '../components/AppTripReview.vue'

Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '/about',
        name: 'About',
        component: About
    },
    {
        path: '/contact',
        name: 'Contact',
        component: Contact
    },
    {
        path: '/buses',
        name: 'Buses',
        component: Buses
    },
    {
        path: '/services',
        name: 'Services',
        component: Services
    },
    {
        path: '/faqs',
        name: 'Faqs',
        component: Faqs
    },
    {
        path: '/portal',
        name: 'Portal',
        component: Portal
    },
    {
        path: '/Passangers',
        name: 'Passangers',
        component: Passangers
    },
    {
        path: '/BusSeats',
        name: 'BusSeats',
        component: BusSeats
    },
    {
        path: '/ReturnBusSeats',
        name: 'ReturnBusSeats',
        component: ReturnBusSeats
    },
    {
        path: '/ReturnBus',
        name: 'ReturnBus',
        component: ReturnBus
    },
    {
        path: '/TripReview',
        name: 'TripReview',
        component: TripReview
    },
    {
        path: '/Payment',
        name: 'Payment',
        component: Payment
    }
]

const router = new VueRouter({
    routes
})

export default router